body {
  background: #F0F2F5;
}

p {
  margin-bottom: 1.2em;
}

.banner {
  display: flex;
  padding: 1em 1.6em;
  background: #e4f7ff;
}

.banner-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 50px;
  margin-right: .6em;
  font-size: 2em;
}

.external-link-icon {
  position: relative;
  top: -1px;
  left: 3px;
  font-size: 9px;
}

.content {
  margin-left: 80px;
  transition: margin-left .2s ease-out;
}

.contentShifted {
  margin-left: 200px;
}

.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
  box-shadow: 0 0 15px 3px rgba(0,0,0,.08);
}

.trigger:hover {
  color: #1890ff;
  box-shadow: 0 0 15px 3px rgba(0,0,0,.1);
}

.logo {
  height: 32px;
  margin: 16px;
  text-indent: 1px;
}

.logo > * {
  letter-spacing: -1.1px;
}

.section {
  margin-bottom: 8em;
}

.section-title {
  text-align: center;
  padding-bottom: .8em;
  text-transform: lowercase;
}

.section#about .section-columns {
  display: flex;
}

.section#about .section-photo {
  width: 400px;
}

.section#about .section-content {
  flex-grow: 1;
}

.photo {
  position: relative;
  top: -50px;
  width: 400px;
  height: 450px;
  background: url(./assets/photo.jpg) no-repeat;
  background-size: contain;
}

.summary {
  margin-bottom: 2em;
}

.social-links {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 20px;
  display: flex;
  justify-content: space-between;
  margin: 0 30px 0 24px;
  line-height: 1;
}

.social-link {
  font-size: 16px;
  color: white;
}

.projects {
  padding: 0 10%;
  display: flex;
  justify-content: space-between;
}

.projects > * {
  align-self: flex-start;
}

.skill-list {
  margin-top: 1.5em;
}

.skill {
  display: flex;
}

.skill-icon {
  width: 60px;
  height: 60px;
  margin-right: 2em;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.skill-progress {
  text-align: left;
  flex-grow: 1;
}

.skill-tag {
  display: inline-block;
  margin-left: .6em;
}

.skill-row:not(:last-child) {
  margin-bottom: 2.5em;
}

.modal-title {
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
}

.technology {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: .5em;
}

.technology-category {
  display: block;
  margin-bottom: .6em;
}

.technology-category{
  margin-top: 2.5em;
}

.technology-icon {
  width: 25px;
  height: 25px;
  margin-right: .6em;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.language-switcher {
  position: fixed;
  right: 2em;
  bottom: 2em;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 160% auto;
  border-radius: 50vh;
  cursor: pointer;
  box-shadow: 0 0 15px 3px rgba(0,0,0,.25);

  transition: transform .1s ease-out,
              box-shadow .2s ease-out;
}

.language-switcher:hover {
  transform: scale(1.07);
  box-shadow: 0 0 20px 4px rgba(0,0,0,.35);
}

.university-logo {
  width: 350px;
  height: 80px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.education-item:not(:last-child) {
  margin-bottom: 1.6em;
}

.contacts {
  display: flex;
  justify-content: space-between;
  padding: 0 2.6em;
}

.project-header {
  display: flex;
  width: 100%;
  height: 200px;
  justify-content: center;
  align-items: center;
}

.project-logo {
  width: 120px;
  height: 120px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.project-tasks {
  margin-top: 3.2em;
}

.project-links {
  display: flex;
  justify-content: center;
  margin-top: 3.2em;
}

.project-links > * {
  margin: 0 1.2em;
}

.structure-module {
  margin-top: 3.2em;
}

.structure-title {
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.2em;
}

.structure-screenshot {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.6em;
}

.structure-screenshot img {
  width: 100%;
}

.structure-description {
  margin-bottom: 2em;
  padding-right: 4em;
}

.timeline-item {
  position: relative;
  padding-bottom: 40px;
}

.timeline-tag-left {
  position: absolute;
  top: 0;
  width: 100%;
  margin-left: calc(-100% - 25px);
  text-align: right;
}

.timeline-tag-right {
  position: absolute;
  top: 0;
  width: 100%;
  margin-left: calc(100% + 30px);
  text-align: left;
}
